<template>
  <div class="about">
    <div>
      <button @click="logout">logout</button>
    </div>
    <div class="space"/>
    <div>
      <div class="twitcharea">
        <iframe src="https://player.twitch.tv/?channel=it0sat0&parent=it0sat0.github.io" 
        frameborder="0" allowfullscreen="true" scrolling="no" height="567" width="930"></iframe>
        <div class="mousebutton" @click="getxy"/>
      </div>
      <div>
        <iframe
          id="chat_embed"
          src="https://www.twitch.tv/embed/it0sat0/chat?parent=it0sat0.github.io"
          height="500"
          width="350">
        </iframe>
      </div>
    </div>
    <div class="uichanger">
      <el-button type="primary" v-on:click="gotoBefore">Before</el-button>
      <!--<el-button type="primary" v-on:click="gotoUI1">After1</el-button>
      <el-button type="primary" v-on:click="gotoUI2">After2</el-button>-->
    </div>
    <!--
    <div class="controller">
       <el-slider v-model="value" max=2 show-stops vertical height="100px"></el-slider>
      <div class="wasd">
        <button @click="colorChanger('w')"> ↑ </button>
        <div class="wasd-list">
          <button @click="colorChanger('a')">←</button>
          <div class="wasd-text" v-if="sendTimer === true"> movement </div>
          <div class="wasd-text" v-if="sendTimer === false"> 送信中 </div>
          <button @click="colorChanger('d')">→</button>
        </div>
        <button @click="colorChanger('s')"> ↓ </button>
      </div>
      <div class="wasd">
        <button @click="colorChanger('vw')"> ↑ </button>
        <div class="wasd-list">
          <button @click="colorChanger('va')">←</button>
          <div class="wasd-text" v-if="sendTimer === true"> view </div>
          <div class="wasd-text" v-if="sendTimer === false"> 送信中 </div>
          <button @click="colorChanger('vd')">→</button>
        </div>
        <button @click="colorChanger('vs')"> ↓ </button>
      </div>
    </div>
    <div class="chest">
      <button @click="colorChanger('c')">click</button>
      <button @click="colorChanger('cr')">craft</button>
      <button @click="colorChanger('op')">chest</button>
      <button @click="colorChanger('cl')">close</button>
    </div>

    <p>channelName:<br/><strong>{{channelName}}</strong></p>
    <p>username:<br/><strong>{{username}}</strong></p>
    <p>password:<br/><strong>{{password}}</strong></p>
    -->
  </div>
  
</template>

<script>
  import firebase from 'firebase'
  import axios from "axios"
  import tmi from "tmi.js"

  export default {
    data() {
      return {
        channelName: this.$_getchannel(),
        username: this.$_getusername(),
        password: this.$_getpassword(),
        channelURL: ``,
        email: this.$_getuseremail(),
        ChatCommand: '',
        sendTimer: true,
        timeoutTimer: '',
        i: 0,
        value: 0
      }
    },
    created() {
      this.channelURL = `https://player.twitch.tv/?channel=${this.channelName}&parent=it0sat0.github.io`
      console.log("channelURL:"+this.channelURL)
    },
    mounted: function(){
      // 初期化処理
      var obj=document.getElementById("ifr")
      obj.removeAttribute("src")
      document.getElementById("ifr").src = this.channelURL
      this.timeout()
    },
    methods: { 
      gotoBefore(){
        this.$router.push('/about')
      },
      gotoUI1(){
        this.$router.push('/ui1')
      },
      gotoUI2(){
        this.$router.push('/ui2')
      },
      colorChanger(command){
        clearTimeout(this.timeoutTimer)
        if(this.sendTimer){
        this.sendTimer = false
        this.post(command)
        console.log("post")
        setTimeout(() => {
            this.sendTimer = true
            }
            ,3000 //時間変更可能
        )
        }
        this.timeout()
      },
      func() {
       console.log(this.channelURL)
      },
      getxy: function(e){
        clearTimeout(this.timeoutTimer)
        console.log("sendTimer"+this.sendTimer)
        if(this.sendTimer){
          this.post("m,"+(e.offsetX/930)+","+(e.offsetY/567))
          console.log("e.offsetX"+ e.offsetX/930)
          console.log("e.offsetY"+ e.offsetY/567)
          this.sendTimer = false
        }
        setTimeout(() => {
          this.sendTimer = true
          }
          ,3000 //時間変更可能
        )
        this.timeout()
      },
      timeout: function(){
        this.timeoutTimer = setTimeout(() => {
          alert("一定時間操作がなかったため、ログアウトします")
          clearTimeout(this.timeoutTimer)
          this.logout()
          }
          ,600000 //時間変更可能
        )
      },
      check: function(){
        console.log(this.channelURL)
        var obj=document.getElementById("ifr")
        obj.removeAttribute("src")
        document.getElementById("ifr").src = this.channelURL
      },
      logout: function(){
        clearTimeout(this.timeoutTimer)
        this.logouotRequest(this.email)
        firebase.auth().signOut();
        clearTimeout(this.timeoutTimer)
        this.$router.push('/')
      },
      logouotRequest: async function(username){
        const params = new URLSearchParams();
        params.append("name",username);
        await axios.post("http://localhost:8040/logout", params)
        //await axios.post("https://asia-east1-twitch-MCI.cloudfunctions.net/logout", params)
        alert("ログアウトしました")
      },
      post(command){
        console.log("OK")
        /*
        const opts = {
          channels: [this.channelName],
          identity: {
            username: this.username,
            password: this.password 
          }
        };
        */
        const opts = {
          channels: ["it0sat0"],
          identity: {
            username: "it0sat0sub",
            password: "oauth:3rl5k8t3wz5q4brrex0alg0efbr1h2"
          }
        };

        // Create a client with our options
        const client = new tmi.client(opts);

        client.connect();

        // Register our event handlers (defined below)
        client.on('connected', onConnectedHandler);

        function onConnectedHandler (addr, port) {
          console.log(`* Connected to ${addr}:${port}`);
          console.log("value:"+this.value)
          for(this.i=0;this.i<this.value;this.i++){
              client.say('it0sat0', `${command}`);
          }
        }
      },
    }
  }
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#nav {
  padding: 30px;
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
}

#nav a.router-link-exact-active {
  color: #42b983;
}

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.signin, .signup{
  margin: 20px;

  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}
input {
  margin: 5px 3px;
  padding: 5px;
}
button {
  margin: 5px;
  padding: 5px;
}
.space{
  margin: 40px;
}
.controller{
  display: flex;
  justify-content: center;
  align-items: center;
}
.twitcharea{
  position: relative;
  height: 567px;
}
.twitchchnnel{
  position: absolute;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, 0%);
}

.uichanger{
  margin: 1em;
}

.mousebutton{
  width: 930px;
  height: 567px;
  position: absolute;
  top: 0;
  left: 0;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, 0%);
  /*background-color: #42b983;*/

}
.wasd{
  margin: 10px;
}

.wasd-list{
  display: flex;
  justify-content: center;
}

.wasd-text{
  width: 5em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.chest{
  margin: 10px;
  justify-content: center;
  align-items: center;
}
</style>
